<template>
	<view class="box">
		<template v-for="(item,k) in message_type">
			<navigator :url="'message_list?notifyType='+item.notifyType" class="message-li" :key="k">
				<view class="left">
					<view class="iconfont" :class="{'iconxiaoxi':item.notifyType=='CHECK_INFO','iconkefu1 bg1':item.notifyType=='SUPPORT_INFO','iconhuo1 bg2':item.notifyType=='SYSTEM_INFO'}"></view>
					<view class="title-box">
						<view class="title">
							{{type_map[item.notifyType]}}
							<view class="dott" v-if="!item.readFlag"></view>
						</view>
						<view class="msg-title">{{item.maxTitle}}{{item.maxTitle}}{{item.maxTitle}}{{item.maxTitle}}</view>
					</view>
				</view>
				<view class="time">
					{{item.maxDate.slice(5)}}
				</view>
			</navigator>
		</template>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				message_type:[],
				type_map:{
					'CHECK_INFO':'审核消息',
					'SUPPORT_INFO':"客服消息",
					'SYSTEM_INFO':'系统消息'
				}
			}
		},
		onShow() {
			this.loadMsg();
		},
		methods: {
			loadMsg() {
				this.$api.get('/o2oNotifyHead/notifyType').then(res => {
					if(res.code==200){
						this.message_type=res.data;
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.box {
		background: white;
	}

	.message-li {
		width: calc(100% - 68rpx);
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 30rpx 0;
		border-bottom: 1px solid #F0F0F0;

		.iconfont {
			width: 84rpx;
			height: 84rpx;
			line-height: 84rpx;
			border-radius: 100%;
			background: #F2AD45;
			color: white;
			font-size: 50rpx;
			margin-right: 30rpx;
			text-align: center;
		}

		.left {
			display: flex;
			align-items: center;

			.title {
				position: relative;
				width: 128rpx;
				.dott {
					position: absolute;
					top: -10rpx;
					right: -16rpx;
					width: 12rpx;
					height: 12rpx;
					border-radius: 100%;
					background: #FA6559;
					z-index: 10;
				}
			}

			.title-box {
				font-size: 20rpx;
				color: #999;

				.title {
					font-size: 30rpx;
					color: #333;
				}
			}
			.msg-title {
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				max-width: 340rpx;
			}
		}

		.time {
			font-size: 20rpx;
			color: #999;
			width: 200rpx;
			text-align: right;
			flex-shrink: 0;
		}

	}

	.bg1 {
		background: #EC8C52 !important;
	}

	.bg2 {
		background: #FF9E97 !important;
	}
</style>
